import axios from 'axios'
import React, { useEffect, useState } from 'react'
import images from './home.module.css'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { createContext } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import MediaItem from '../MediaItem/MediaItem';
import { useFormik } from 'formik';
import { Helmet } from 'react-helmet';

export default function Home() {

  let Navigate = useNavigate()

  const [Loading, setLoading] = useState(false)

  const [Movie, setMovie] = useState([])
  const [Tv, setTv] = useState([])
  const [id, setid] = useState([])
  const [People, setPeople] = useState([])
  const [search, setsearch] = useState('')

  async function GetTrending(mediaType, callback) {
    setLoading(true)
    let { data } = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=d733d821ff1062c1d16db10d624504c3`)
    callback(data.results)
    setLoading(false)

  }


  useEffect(() => {
    GetTrending('movie', setMovie)
    GetTrending('tv', setTv)
    GetTrending('person', setPeople)
  }, [])

  let Formik = useFormik({
    initialValues: {
      search: ''

    },
    onSubmit: GetSearch,

  })
  async function GetSearch(med) {
    console.log(med);
    let { data } = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=d733d821ff1062c1d16db10d624504c3&query=${med.search}`)
    console.log(data.results);
    setsearch(data.results);
  }
  

  return <>
     <Helmet>
      <title>Home</title>
    </Helmet>
    {Loading ?
      <div className='loading d-flex align-items-center justify-content-center  position-fixed end-0  start-0 bottom-0 '>

        <div>
          <div className="lds-ring"><div></div><div></div><div></div><div></div></div>
        </div>      </div>
      : <>
        <div className={images.header}>
          <div className={images.overlay}>
            <div className="d-flex align-items-center justify-content-center">
              <div className='container'>
                <h2 className='h3 text-white text-center'>Welcome.<p>Millions of movies, TV shows and people to discover. Explore now.
                </p>
                </h2>
                <form id="inner_search_form" onSubmit={Formik.handleSubmit} className="position-relative w-100">
                  <input name="search" id='searchbar' value={Formik.values.search} onChange={Formik.handleChange} type="search" className={images.search + " form-control w-100 text-center rounded-5"} placeholder="Search for a movie ......." />

                  <input type="submit" className={images.btn + " btn rounded-5 position-absolute end-0 top-0"} value="Search" />
                </form>
              </div>
            </div>
          </div>
        </div>
        {search != '' ? <div className='container'>
          <div className='row pt-5 '>
            {search.map((items, index) => {
              return <>   <div id={index} className='col-lg-2 col-md-4'>
                <Link key={items.id} className='text-decoration-none text-white' to={`/MoviesDetails/${items.id}/movie`}>
                  <div className='position-relative'>
                    <div className=''>
                      {items.poster_path ? <img src={'https://image.tmdb.org/t/p/w500' + items.poster_path} height={210} className='w-100' alt="" /> : <img src={'https://image.tmdb.org/t/p/w500' + items.profile_path} height={350} className='w-100' alt="" />}
                    </div>
                    <h3 className='h6 my-3'>{items.name} {items.title}</h3>
                    {items.vote_average ? <div className='vote top-0 end-0 position-absolute pt-1'>{items.vote_average.toFixed(1)}</div> : ''}
                  </div></Link>
              </div>
              </>

            })}
          </div>

        </div> : <>
          <section className={images.Home + ' pt-5'}>
            <div className='container'>
              <div className='row g-3 py-3'>
                <div className="col-md-4  d-flex align-items-center">
                  <div>
                    <div className='pading bg-light w-25 mb-3'></div>
                    <h2>Trending movies <span className='h2 d-block'>to watch Rigth Now</span> </h2>
                    <p>most watched movies by days</p>
                    <div className='pading bg-light w-50 mt-3'></div>
                  </div>
                </div>
                {Movie.slice(0, 10).map((items, index) => {
                  return <MediaItem items={items} key={index} />
                })}

              </div>
              <div className='row g-3 py-3'>
                <div className="col-md-4 d-flex align-items-center">
                  <div>
                    <div className='pading bg-light w-25 mb-3'></div>
                    <h2>Trending Tv <span className='h2 d-block'>to watch Rigth Now</span> </h2>
                    <p>most watched movies by days</p>
                    <div className='pading bg-light w-50 mt-3'></div>
                  </div>
                </div>
                {Tv.slice(0, 10).map((items, index) => {
                  return <MediaItem key={index} items={items} />
                })}

              </div>
              <div className='row g-3 py-3'>
                <div className="col-md-4 d-flex align-items-center">
                  <div>
                    <div className='pading bg-light w-25 mb-3'></div>
                    <h2>Trending People <span className='h2 d-block'>to watch Rigth Now</span> </h2>
                    <p>most watched movies by days</p>
                    <div className='pading bg-light w-50 mt-3'></div>
                  </div>
                </div>
                {People.slice(0, 10).map((items, index) => {
                  return <MediaItem items={items} key={index} />
                })}

              </div>
            </div>
          </section>
        </>}
      </>}
  </>
}
