import axios from 'axios'
import { Formik, useFormik } from 'formik'
import React, { useState } from 'react'
import { Navigate, useNavigate } from 'react-router-dom'
import *as Yup from 'yup'
import { Helmet } from "react-helmet";
import '../Login/Login.css'

export default function () {
    let [loading, Setloading] = useState(false)
    let Navigate = useNavigate()
    let BaseUrl = `https://route-ecommerce.onrender.com`
    const [MessErrors, setMessErrors] = useState()
    const [ResetCode, setResetCode] = useState(true)
    let validationSchema = Yup.object({
        email: Yup.string().required('email is required').email('Invalid email address')
    })

    let Formik1 = useFormik({
        initialValues: {
            email: ''
        },
        onSubmit: async (values) => {
            Setloading(true)
            let { data } = await axios.post(`${BaseUrl}/api/v1/auth/forgotPasswords`, values).catch((error) => {
                setMessErrors(error.response.data.message);
                Setloading(false)
            })
            console.log(data);
            if (data.statusMsg === "success") {
                setResetCode(false)
                Setloading(false)

            }

        },
        validationSchema
    })
    let validat = Yup.object({
        resetCode: Yup.string().required('verifyResetCode is required').matches(/^[0-9]{1,6}$/, 'Invalid verifyResetCode number')
    })


    let Formik2 = useFormik({
        initialValues: {
            resetCode: ''
        },
        onSubmit: async (values) => {
            Setloading(true)
            let { data } = await axios.post(`${BaseUrl}/api/v1/auth/verifyResetCode`, values).catch((errors) => {
                 setMessErrors(errors.response.data.message)
                 Setloading(false)
                })
            console.log(data);

            if (data.status === "Success") {
                Navigate('/resetPassword')
                console.log(data);
                Setloading(false)
            }

        },
        validationSchema: validat
    })


    return <>
        <Helmet>
            <title>Forget password</title>
        </Helmet>
        {ResetCode ?
            <div className='container main'>
                <div className='row bg-white p-4'>
                    <div className="col-md-5">
                        <div className="input-box">
                            <header className='h2'>Forget password</header>
                            {MessErrors ? <div className='alert alert-danger'>{MessErrors}</div> : ''}

                            <form onSubmit={Formik1.handleSubmit}>
                                <div className="input-field">
                                    <input type="email" className="input" onChange={Formik1.handleChange} onBlur={Formik1.handleBlur} value={Formik1.values.email} name='email' id="email" required />
                                    <label htmlFor="email">Email</label>
                                    {Formik1.errors.email ? <div className='alert alert-danger mt-2'>{Formik1.errors.email}</div> : ''}

                                </div>
                                <div className="input-field">
                                    {loading ? <button type='button' className='submit main-color  text-white'><i className='fas fa-spinner fa-spin'></i></button>
                                        : <button disabled={!(Formik1.isValid && Formik1.dirty)} type='submit' className='submit mt-1'>Send Email</button>
                                    }
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div> :
            <>
                <div className='container main'>
                    <div className='row bg-white p-4'>
                        <div className="col-md-5">
                            <div className="input-box">
                                <header className='h2'>Forget password</header>
                                {MessErrors ? <div className='alert alert-danger'>{MessErrors}</div> : ''}

                                <form onSubmit={Formik2.handleSubmit}>
                                    <div className="input-field">
                                        <input type="text" className="input" onChange={Formik2.handleChange} onBlur={Formik2.handleBlur} value={Formik2.values.resetCode} name='resetCode' id="resetCode" required />
                                        <label htmlFor="resetCode">resetCode</label>
                                        {Formik2.errors.resetCode ? <div className='alert alert-danger mt-2'>{Formik2.errors.resetCode}</div> : ''}

                                    </div>


                                    <div className="input-field">
                                        {loading ? <button type='button' className='submit main-color  text-white'><i className='fas fa-spinner fa-spin'></i></button>
                                            : <button disabled={!(Formik2.isValid && Formik2.dirty)} type='submit' className='submit mt-1'>Verify Code</button>
                                        }
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        }



    </>
}
