import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet'
import { Link } from 'react-router-dom'

export default function People() {
  const [Loading, setLoading] = useState(false)
  const [Movie, setMovies] = useState([])
  const [page, setpage] = useState({})
  let nums = new Array(10).fill(1).map((ele, index) => index + 1)
  console.log(nums);
  async function GetTrending(page) {
    setLoading(true)
    let { data } = await axios.get(`https://api.themoviedb.org/3/person/popular?api_key=d733d821ff1062c1d16db10d624504c3&language=en-US&page=${page}`)
    console.log(data.results);
    setMovies(data.results);
    setLoading(false)
    setpage(page)
    // console.log(page)
  }
  useEffect(() => {
    GetTrending('1')
  }, [])
  return <>
   <Helmet>
      <title>People</title>
    </Helmet>
    {Loading ?
      <div className='loading d-flex align-items-center justify-content-center  position-fixed end-0  start-0 bottom-0 '>

        <div>
          <div className="lds-ring"><div></div><div></div><div></div><div></div></div>
        </div>      </div>
      : <>
        <div className='container'>
          <div className='row py-4 g-3'>
            {Movie.map((Movies) => {
              return <div key={Movies.id} className='col-lg-2 col-md-4'>
                <Link className='text-decoration-none text-white' to={`/MoviesDetails/${Movies.id}/person`}>
                  <div className='cared position-relative'>
                    <div class="card nav-bg border-0">
                      <img src={'https://image.tmdb.org/t/p/w500' + Movies.profile_path} height={300} class="card-img-top" alt="..."/>
                        <div class="card-body">
                          <p class="card-text">{Movies.name}</p>
                        </div>
                    </div>
                    {/* <div className=''>
                      <img src={'https://image.tmdb.org/t/p/w500' + Movies.profile_path} height={300} className='w-100 side-images' alt="" />
                    </div>
                    <h3 className='h6 my-2'>{Movies.name}</h3>
                    {Movies.vote_average ? <div className='vote top-0 end-0 position-absolute pt-1'>{Movies.vote_average.toFixed(1)}</div> : ''}
                */}  </div> 
                  </Link>
              </div>

            })}

          </div>
          <nav aria-label="Page navigation example">
            <ul className="pagination justify-content-center">
              {/* {page > 1 ? <li className="page-item ">
            <Link className="page-link" onClick={() => { GetTrending(page - 1) }} aria-disabled="false">Previous</Link>
          </li> : <li className="page-item disabled">
            <Link className="page-link" onClick={() => { GetTrending(page - 1) }} aria-disabled="false">Previous</Link>
          </li>} */}
              {nums.map((pages) => {
                return <li onClick={() => { GetTrending(pages) }} key={pages} className="page-item"><a className="page-link" href="#">{pages}</a></li>
              })}
              {/* <li className="page-item">
            <Link className="page-link" onClick={ (e,index) => { GetTrending(page+index) }}>Next</Link>
          </li> */}
            </ul>
          </nav>
        </div >
      </>
    }

  </>
}
